import styles from './Main.module.scss';
import { useEffect, useState } from 'react';
import Button from 'components/common/Button';
import Modal from './Modal';
import Title from 'components/common/PageTitle';
import { userInfo, userDelete } from 'api/fetcher';
import { clearLocalStorage } from 'api/token';

interface UserData {
  id: string;
  name: string;
  email: string;
  phoneNumber: string;
  profileImage: string;
}

function Main() {
  const [showEditModal, setShowEditModal] = useState<boolean>(false);
  const [showRemoveModal, setShowRemoveModal] = useState<boolean>(false);
  const [userData, setUserData] = useState<UserData>({
    id: '',
    name: '',
    email: '',
    phoneNumber: '',
    profileImage: '',
  });

  // Edit 
  const handleShowEditModal = (): void => {
    setShowEditModal((prevState) => !prevState);
  };
  // Remove 
  const handleShowRemoveModal = (): void => {
    setShowRemoveModal((prevState) => !prevState);
  };

  // Edit  
  const handleNavigateToEdit = (): void => {
    window.location.href = '/mypage/edit';
  };

  //  
  const handleRemoveAccount = (): void => {
    userDelete();
    clearLocalStorage();
    alert('  .  EarF  .');
    window.location.href = '/';
  };

  //   
  useEffect(() => {
    const fetchUserInfo = async () => {
      try {
        const { name, email, id, phoneNumber, profileImage }: UserData = (await userInfo()) as UserData;
        const userData = {
          name,
          email,
          id,
          phoneNumber,
          profileImage,
        };
        setUserData(userData);
      } catch (error) {
        console.error('Error fetching user info:', error);
      }
    };
    fetchUserInfo();
  }, []);

  return (
    <>
      <div className={styles.main}>
        <Title title='' />
        <div className={styles.contents}>
          <div className={styles.profile}>
            <div className={styles.imgContainer}>
              <img src={userData.profileImage} alt='' />
            </div>
          </div>

          <div className={styles.dataFiledSet}>
            <div className={styles.dataFiled}>
              <div className={styles.fixedData}>
                <span></span>
              </div>
              <div className={styles.fetchData}>{userData.name}</div>
            </div>
            <div className={styles.dataFiled}>
              <div className={styles.fixedData}>
                <span></span>
              </div>
              <div className={styles.fetchData}>{userData.id}</div>
            </div>
            <div className={styles.dataFiled}>
              <div className={styles.fixedData}>
                <span></span>
              </div>
              <div className={styles.fetchData}>{userData.email}</div>
            </div>
            <div className={styles.dataFiled}>
              <div className={styles.fixedData}>
                <span></span>
              </div>
              <div className={styles.fetchData}>{userData.phoneNumber}</div>
            </div>
          </div>
          <div className={styles.buttonContainer}>
            <Button text={''} onClick={handleShowEditModal} />
            <Button text={''} className={'whiteButton'} onClick={handleShowRemoveModal} />
          </div>
        </div>
      </div>
      {showEditModal && (
        <Modal title={''} handleShowModal={handleShowEditModal} handleNextAction={handleNavigateToEdit} />
      )}
      {showRemoveModal && (
        <Modal title={''} handleShowModal={handleShowRemoveModal} handleNextAction={handleRemoveAccount} />
      )}
    </>
  );
}

export default Main;
